.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  text-align: center;
  cursor: pointer;
  color: #212121;
  background-color: rgba(0, 0, 0, .12);
  border: 2px solid #000;
  border-radius: 100px;
  padding: 8px 12px;
  font-weight: bold;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.feedback-form button:hover, .feedback-form button:focus {
  color: #fafafa;
  background-color: #212121;
  border-color: rgba(0, 0, 0, 0);
}

input, textarea {
  font: inherit;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  padding: 8px;
}

textarea {
  resize: none;
}

